import React, { useState, useEffect, useRef } from "react";
import { Button, CircularProgress, FormControl, InputLabel, MenuItem, Select, Radio, RadioGroup, FormControlLabel, Typography, Container, Card, CardContent } from "@mui/material";
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { useNavbarVisibility } from "./NavbarVisibilityContext";
import { useNavigate } from "react-router-dom";

const QuizComponent = () => {
    const { navbarVisible, setNavbarVisible } = useNavbarVisibility();
    const navigate = useNavigate();

    const [questions, setQuestions] = useState([]);
    const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
    const [selectedAnswer, setSelectedAnswer] = useState("");
    const [score, setScore] = useState(0);
    const [loading, setLoading] = useState(false);
    const [quizStarted, setQuizStarted] = useState(false);
    const [quizFinished, setQuizFinished] = useState(false);
    const [numQuestions, setNumQuestions] = useState(10);
    const [category, setCategory] = useState("");
    const [difficulty, setDifficulty] = useState("");
    const [shuffledAnswers, setShuffledAnswers] = useState([]);
    // Timer state
    const [timerMinutes, setTimerMinutes] = useState(5);
    const [timeLeft, setTimeLeft] = useState(0);
    const timerRef = useRef(null);

    // Hide navbar when quiz starts, show when quiz ends/exits
    useEffect(() => {
        if (quizStarted && !quizFinished) {
            setNavbarVisible(false);
        } else {
            setNavbarVisible(true);
        }
    }, [quizStarted, quizFinished, setNavbarVisible]);

    const shuffleAnswers = (currentQuestion) => {
        if (!currentQuestion) return [];
        const answers = [...currentQuestion.incorrect_answers, currentQuestion.correct_answer];
        for (let i = answers.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [answers[i], answers[j]] = [answers[j], answers[i]];
        }
        return answers;
    };

    useEffect(() => {
        if (quizStarted) {
            setLoading(true);
            fetch(
                `http://localhost:8788/api/quiz/questions?amount=${numQuestions}&category=${category}&difficulty=${difficulty}`
            )
                .then((response) => response.json())
                .then((data) => {
                    if (data.results && data.results.length > 0) {
                        setQuestions(data.results);
                        setShuffledAnswers(shuffleAnswers(data.results[0]));
                    } else {
                        console.log("No quiz data available.");
                    }
                    setLoading(false);
                })
                .catch((error) => {
                    console.log("Error fetching quiz data:", error);
                    setLoading(false);
                });
        }
    }, [quizStarted, numQuestions, category, difficulty]);

    useEffect(() => {
        if (questions[currentQuestionIndex]) {
            setShuffledAnswers(shuffleAnswers(questions[currentQuestionIndex]));
        }
    }, [questions, currentQuestionIndex]);

    // Timer logic useEffect at top level
    useEffect(() => {
        if (quizStarted && !quizFinished && timeLeft > 0) {
            timerRef.current = setTimeout(() => setTimeLeft(timeLeft - 1), 1000);
        } else if (quizStarted && !quizFinished && timeLeft === 0) {
            setQuizFinished(true);
            setNavbarVisible(true);
            navigate("/");
        }
        return () => clearTimeout(timerRef.current);
    }, [quizStarted, quizFinished, timeLeft, setNavbarVisible, navigate]);

    // Format time in mm:ss
    const formatTime = (seconds) => {
        const m = Math.floor(seconds / 60).toString().padStart(2, '0');
        const s = (seconds % 60).toString().padStart(2, '0');
        return `${m}:${s}`;
    };

    const startQuiz = () => {
        setQuizStarted(true);
        setScore(0);
        setCurrentQuestionIndex(0);
        setQuizFinished(false);
        setTimeLeft(timerMinutes * 60); // Start timer in seconds
    };

    const exitQuiz = () => {
        setQuizStarted(false);
        setQuizFinished(false);
        setScore(0);
        setCurrentQuestionIndex(0);
        setTimeLeft(0);
        setNavbarVisible(true);
    };

    const handleAnswerSelect = (event) => {
        setSelectedAnswer(event.target.value);
    };

    const nextQuestion = () => {
        if (selectedAnswer === questions[currentQuestionIndex]?.correct_answer) {
            setScore(score + 1);
        }
        setSelectedAnswer("");
        setCurrentQuestionIndex(currentQuestionIndex + 1);
    };

    const previousQuestion = () => {
        setSelectedAnswer("");
        setCurrentQuestionIndex(currentQuestionIndex - 1);
    };

    const finishQuiz = () => {
        if (selectedAnswer === questions[currentQuestionIndex]?.correct_answer) {
            setScore(score + 1);
        }
        setQuizFinished(true);
        setNavbarVisible(true);
    };

    if (loading) {
        return (
            <div style={{ textAlign: "center" }}>
                <CircularProgress />
                <Typography>Loading...</Typography>
            </div>
        );
    }

    if (!quizStarted) {
        return (
            <Container>
                <Typography variant="h4">Welcome to the Quiz!</Typography>
                <div className="settings">
                    <FormControl fullWidth margin="normal">
                        <InputLabel>Quiz Duration (minutes)</InputLabel>
                        <Select value={timerMinutes} onChange={e => setTimerMinutes(Number(e.target.value))} label="Quiz Duration (minutes)">
                            {[1, 3, 5, 10, 15, 20, 30].map(val => (
                                <MenuItem key={val} value={val}>{val} min</MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <FormControl fullWidth margin="normal">
                        <InputLabel>Number of Questions</InputLabel>
                        <Select
                            value={numQuestions}
                            onChange={(e) => setNumQuestions(e.target.value)}
                            label="Number of Questions"
                        >
                            <MenuItem value={5}>5</MenuItem>
                            <MenuItem value={10}>10</MenuItem>
                            <MenuItem value={15}>15</MenuItem>
                            <MenuItem value={20}>20</MenuItem>
                            <MenuItem value={25}>25</MenuItem>
                        </Select>
                    </FormControl>

                    <FormControl fullWidth margin="normal">
                        <InputLabel>Select Category</InputLabel>
                        <Select value={category} onChange={(e) => setCategory(e.target.value)} label="Select Category">
                            <MenuItem value="">Any Category</MenuItem>
                            <MenuItem value="10">Entertainment: Books</MenuItem>
                            <MenuItem value="11">Entertainment: Film</MenuItem>
                            <MenuItem value="12">Entertainment: Music</MenuItem>
                            <MenuItem value="13">Entertainment: Musicals &amp; Theatres</MenuItem>
                            <MenuItem value="14">Entertainment: Television</MenuItem>
                            <MenuItem value="15">Entertainment: Video Games</MenuItem>
                            <MenuItem value="16">Entertainment: Board Games</MenuItem>
                            <MenuItem value="17">Science &amp; Nature</MenuItem>
                            <MenuItem value="18">Science: Computers</MenuItem>
                            <MenuItem value="19">Science: Mathematics</MenuItem>
                            <MenuItem value="20">Mythology</MenuItem>
                            <MenuItem value="21">Sports</MenuItem>
                            <MenuItem value="22">Geography</MenuItem>
                            <MenuItem value="23">History</MenuItem>
                            <MenuItem value="24">Politics</MenuItem>
                            <MenuItem value="25">Art</MenuItem>
                            <MenuItem value="26">Celebrities</MenuItem>
                            <MenuItem value="27">Animals</MenuItem>
                            <MenuItem value="28">Vehicles</MenuItem>
                            <MenuItem value="29">Entertainment: Comics</MenuItem>
                            <MenuItem value="30">Science: Gadgets</MenuItem>
                            <MenuItem value="31">Entertainment: Japanese Anime &amp; Manga</MenuItem>
                            <MenuItem value="32">Entertainment: Cartoon &amp; Animations</MenuItem>
                            {/* Add more categories */}
                        </Select>
                    </FormControl>

                    <FormControl fullWidth margin="normal">
                        <InputLabel>Select Difficulty</InputLabel>
                        <Select value={difficulty} onChange={(e) => setDifficulty(e.target.value)} label="Select Difficulty">
                            <MenuItem value="">Any Difficulty</MenuItem>
                            <MenuItem value="easy">Easy</MenuItem>
                            <MenuItem value="medium">Medium</MenuItem>
                            <MenuItem value="hard">Hard</MenuItem>
                        </Select>
                    </FormControl>

                    <Button variant="contained" color="primary" onClick={startQuiz}>
                        Start Quiz
                    </Button>
                    <Button variant="outlined" color="secondary" onClick={exitQuiz}>
                        Exit
                    </Button>
                </div>
            </Container>
        );
    }

    if (quizFinished) {
        return (
            <Container maxWidth="sm" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '70vh' }}>
                <Card elevation={6} style={{ padding: 32, borderRadius: 18, background: 'linear-gradient(135deg, #f3e7fa 0%, #c9e4ff 100%)', boxShadow: '0 8px 32px rgba(80,0,120,0.12)' }}>
                    <CardContent style={{ textAlign: 'center' }}>
                        <EmojiEventsIcon style={{ fontSize: 60, color: '#a259f7', marginBottom: 12, animation: 'pop 0.5s' }} />
                        <Typography variant="h4" style={{ fontWeight: 700, color: '#7c3aed', marginBottom: 8 }}>Quiz Finished!</Typography>
                        <Typography variant="h6" style={{ marginBottom: 20, color: '#333' }}>Your Score: <span style={{ color: '#00b894' }}>{score}</span> / {questions.length}</Typography>
                        <Button variant="contained" color="primary" size="large" style={{ borderRadius: 20, fontWeight: 600, background: 'linear-gradient(90deg, #7c3aed, #a259f7)' }} onClick={() => navigate("/")}>Go Home</Button>
                    </CardContent>
                </Card>
            </Container>
        );
    }

    if (currentQuestionIndex >= questions.length) {
        return (
            <Container maxWidth="sm" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '70vh' }}>
                <Card elevation={6} style={{ padding: 32, borderRadius: 18, background: 'linear-gradient(135deg, #e0c3fc 0%, #8ec5fc 100%)', boxShadow: '0 8px 32px rgba(80,0,120,0.12)' }}>
                    <CardContent style={{ textAlign: 'center' }}>
                        <CheckCircleIcon style={{ fontSize: 56, color: '#00b894', marginBottom: 12, animation: 'pop 0.5s' }} />
                        <Typography variant="h4" style={{ fontWeight: 700, color: '#00b894', marginBottom: 8 }}>Quiz Completed!</Typography>
                        <Typography variant="h6" style={{ marginBottom: 20, color: '#333' }}>Your score: <span style={{ color: '#7c3aed' }}>{score}</span></Typography>
                        <Button variant="contained" color="primary" size="large" style={{ borderRadius: 20, fontWeight: 600, background: 'linear-gradient(90deg, #00b894, #8ec5fc)' }} onClick={finishQuiz}>
                            Finish Quiz
                        </Button>
                    </CardContent>
                </Card>
            </Container>
        );
    }

    const currentQuestion = questions[currentQuestionIndex];

    // During quiz: hide navbar, show timer and only a Finish button
    return (
        <Container>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 24 }}>
                <Typography variant="h5" color="primary">Quiz In Progress</Typography>
                <Typography variant="h6" color="secondary">Time Left: {formatTime(timeLeft)}</Typography>
                <Button variant="contained" color="error" onClick={() => { finishQuiz(); navigate("/"); }}>Finish</Button>
            </div>
            <Typography variant="h6">Question {currentQuestionIndex + 1} / {questions.length}</Typography>
            <Typography variant="subtitle1">Category: {currentQuestion?.category}</Typography>
            <Typography variant="subtitle2">Difficulty: {currentQuestion?.difficulty}</Typography>
            <Typography variant="h6" style={{ margin: '16px 0' }}>{currentQuestion?.question}</Typography>
            <RadioGroup
                value={selectedAnswer}
                onChange={handleAnswerSelect}
                name="answers"
                aria-label="answers"
            >
                {shuffledAnswers.map((answer, index) => (
                    <FormControlLabel
                        key={index}
                        value={answer}
                        control={<Radio />}
                        label={answer}
                    />
                ))}
            </RadioGroup>
            <div style={{ marginTop: 16 }}>
                {currentQuestionIndex > 0 && (
                    <Button variant="outlined" color="secondary" onClick={previousQuestion} style={{ marginRight: 8 }}>
                        Back
                    </Button>
                )}
                {currentQuestionIndex < questions.length - 1 ? (
                    <Button variant="contained" color="primary" onClick={nextQuestion}>
                        Next
                    </Button>
                ) : (
                    <Button variant="contained" color="primary" onClick={finishQuiz}>
                        Finish Quiz
                    </Button>
                )}
            </div>
            <Typography variant="body1" style={{ marginTop: 16 }}>Score: {score}</Typography>
        </Container>
    );
};

export default QuizComponent;
